.App {
  text-align: center;
  width: 100%;
  height: 100vh;
  /* background-color: black; */
}
.NavMenu {
  position: fixed;
  width: 100%;
}
button {
  cursor: pointer;
}
.AddMenu {
  width: 100%;
  height: 100vh;
  background-color: #000000;
  margin: 0% auto;
  overflow-x: scroll;
}
::placeholder {
  color: white;
  font-weight: 600;
}
.BlazingImage {
  width: 400px;
  margin: 2% auto;
}
.form {
  margin: 2% auto;
  background: #f7941d;
  width: 50%;
  height: auto;
  border-radius: 10px;
  padding-top: 10px;
  padding-bottom: 20px;
}
.form > input {
  background-color: #000000;
  border: 1px solid white;
  border-radius: 5px;
  width: 80%;
  height: 40px;
  font-family: "Libre Caslon Text", Sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: white;
}
.form > select {
  background-color: #000000;
  color: white;
  border: 1px solid white;
  width: 81% !important;
  font-size: 20px;
  height: 40px;
  border-radius: 5px;
  font-family: "Libre Caslon Text", Sans-serif;
  font-weight: 400;
}
.form > button {
  width: 81% !important;
  height: 40px;
  background-color: #000000;
  border: 1px solid white;
  border-radius: 5px;
  font-size: 20px;
  color: white;
  font-family: "Libre Caslon Text", Sans-serif;
  font-weight: 400;
}

/* Navigation Bar */
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #000000;
}

li {
  float: left;
}

li a {
  display: block;
  font-family: "Libre Caslon Text", Sans-serif;
  color: white;
  font-size: 25px;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  cursor: pointer;
}
.active {
  background-color: #f7941d;
}
.Menu {
  width: 90%;
  height: 800px;
  margin: 0% auto;
  /* background-color: black; */
}
.menuNameImage {
  display: grid;
  grid-template-columns: auto;
  width: auto;
  height: auto;
  margin: 10px auto;
  cursor: pointer;
}
.itemName {
  color: #ffffff;
  font-family: "Libre Caslon Text", Sans-serif;
  font-size: 40px;
  font-weight: 500;
}

.items {
  display: grid;
  grid-template-columns: auto auto auto;
  margin: 5% auto;
}
.MenuImage {
  width: 300px;
  height: 200px;
  margin: 10% auto;
}
.sectionImage {
  width: 300px;
  height: 200px;
  margin: 3% auto;
}
.Home {
  width: 100%;
  height: 100vh;
  background-color: #000000;
  overflow-x: scroll;
  /* padding: 20px; */
}
.BackItemsMenu {
  color: #ffffff;
  background-color: #f7941d;
  border-radius: 15px;
  /* float: right; */
  position: absolute;
  top: 20%;
  right: 10%;
  width: 200px;
  font-family: "Libre Caslon Text", Sans-serif;
  font-size: 45px;
  font-weight: 500;
}
.ItemsSections {
  width: 80%;
  overflow-x: scroll;
  height: 550px;
  background-color: #f7941d;
  border-radius: 10px;
  margin: 5% auto;
  padding-bottom: 20px;
}
.ItemsSections > h1 {
  color: #ffffff;
  font-family: "Libre Caslon Text", Sans-serif;
  font-size: 45px;
  font-weight: 500;
}

.HomeFoodItemName {
  width: 96%;
  height: 120px;
  display: grid;
  margin: 10px auto;
  border-bottom: 4px solid white;
  padding: 20px;
  text-align: left;
  border-radius: 10px;
  grid-template-columns: 85% auto;
}
.FuncButtons {
  width: 100px;
  height: 50px;
  /* color: #f7941d; */
  color: black;
  border: none;
  font-size: 20px;
  font-family: "Libre Caslon Text", Sans-serif;
  font-weight: 800;
  border-radius: 10px;
  margin-left: auto;
  margin-right: 0%;
  /* border: 1px solid; */
}
.inputDetails {
  display: grid;
  grid-template-columns: auto;
  margin-top: -10px;
}
.ButtonsDiv {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: auto;
}
#input0,
.input0 {
  border: none;
  height: 40px;
  border-radius: 5px;
  background-color: #f7941d;
  font-weight: 800;
  margin-bottom: 5px;
  font-size: 25px;
  border: 0.5px solid white;
}
.input0:nth-child(3) {
  font-size: 20px;
}

@media only screen and (max-width: 800px) {
  text {
    display: none;
  }
  .menuNameImage {
    width: 150px;
    height: auto;
  }
  .MenuImage {
    width: 140px;
    height: 100px;
  }
  .items {
    grid-template-columns: auto auto;
    margin: 15% auto;
  }
  .Menu {
    height: 100vh;
  }
  .AddMenu {
    width: 100%;
    color: white;
  }
  .form > input,
  .form > select,
  .form > button {
    width: 80%;
    height: 30px;
  }
  .BlazingImage {
    width: 300px;
    margin: 12% auto;
  }
  .itemName {
    font-size: 25px;
  }
  li a {
    font-size: 20px;
  }
  .form {
    width: 80%;
    padding-bottom: 10px;
    padding-top: 10px;
    margin: 0% auto;
  }
  .BackItemsMenu {
    font-size: 25px;
    position: inherit;
    margin-bottom: 20px;
  }
  .ItemsSections {
    width: 96%;
    height: auto;
    border-radius: 4px;
  }
  #input0,
  .input0 {
    font-size: 17px;
    height: auto;
  }
  .input0:nth-child(3) {
    font-size: 12px;
  }
  .HomeFoodItemName {
    width: 90%;
    height: 100px;
    grid-template-columns: auto 25%;
  }
  .FuncButtons {
    width: 70px;
    height: 40px;
    font-size: 14px;
  }
  .ItemName {
    top: 65px !important;
    font-size: 30px !important;
  }
  .sectionImage {
    margin: 15% auto;
  }
  .Home {
    height: auto;
  }
}
